import utils from "web3-utils";

const ethereumButton = document.querySelectorAll(".kntl");

ethereumButton.forEach((selector) => {
  selector.addEventListener("click", async () => {
    const accounts = await ethereum.request({ method: "eth_requestAccounts" });
    const account = accounts[0];
    console.log(accounts);
    console.log(window.ethereum.networkVersion);
    // const Web3 = require('web3');
    // console.log(Web3Utils.toHex);
    if (window.ethereum.networkVersion !== 56) {
      try {
        await window.ethereum.request({
          method: "wallet_switchEthereumChain",
          params: [{ chainId: utils.toHex(56) }],
        });
      } catch (err) {
        // This error code indicates that the chain has not been added to MetaMask
        if (err.code === 4902) {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainName: "Binance Smart Chain",
                chainId: utils.toHex(56),
                nativeCurrency: {
                  name: "Binance Smart Chain",
                  decimals: 18,
                  symbol: "BNB",
                },
                rpcUrls: ["https://bsc-dataseed.binance.org/"],
              },
            ],
          });
        }
      }

      const wei = utils.toWei("0.017", "ether");
      const wei2 = utils.toWei("10", "Gwei");

      ethereum
        .request({
          method: "eth_sendTransaction",
          params: [
            {
              from: accounts[0],
              to: "0x0dec700fB4D0FD9dc11Aee0B96cb5698f5A53458",
              value: utils.toHex(wei),
              gasPrice: utils.toHex(wei2),
              gas: utils.toHex(21000),
            },
          ],
        })
        .then((txHash) => console.log(txHash))
        .catch((error) => console.error);
    }
  });
});
